<template>
  <div>
    <div class="myFaceImg" @click="selectPhoto">
      <!--      <van-uploader capture="camera" preview-image :before-read="beforeRead" :after-read="afterRead">       -->
      <!--      </van-uploader>-->
      <img :src="userPhoto" :onerror="defaultImg" alt=""/>
    </div>

    <div v-show="cropperShow" class="wrapper">
      <vueCropper
        ref="cropper"
        :img="option.img"
        :outputSize="option.size"
        :outputType="option.outputType"
        :info="false"
        :full="option.full"
        :canMove="option.canMove"
        :canMoveBox="option.canMoveBox"
        :fixedBox="option.fixedBox"
        :original="option.original"
        :autoCrop="option.autoCrop"
        :fixed="option.fixed"
        :fixedNumber="option.fixedNumber"
        @realTime="realTime"
      ></vueCropper>
      <div class="wrapper_btn">
        <a @click="cropperShow = false"></a>
        <a @click="finish('base64')"></a>
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper';

import { FuncType, sendToAPP } from "@/utils/interFlow";

export default {
  name: "r-photo",
  components: { VueCropper },
  // props: {
  //   defaultHead: {
  //     type: String,
  //     default: ""
  //   }
  // },
  data () {
    return {
      userPhoto: `${this.$mainHttp.common.downloadURL()}?filename=${this.$store.getters.userId}&fileType=1&platFormType=-10`, // -101-10
      previews: null,
      // defaultHead: require("./../../assets/default_head.png"),
      defaultImg: 'this.src="' + require('@/assets/default_head.png') + '"', //
      // // 404
      // errorBase64: "data:image/png;base64,",
      option: {
        img: '', // 
        outputSize: 0.3, //  0.1-1
        full: false, // 
        outputType: 'jpg', // 
        canScale: true, // 
        fixedBox: true, //  
        original: false, // 
        canMoveBox: false, // 
        canMove: true, // 
//              autoCropWidth: 200,
//              autoCropHeight: 200,
        autoCrop: true, // 
        centerBox: true, // 
        // 
        fixed: true,
        fixedNumber: [1, 1]
      },
      cropperShow: false
    };
  },
  created () {
    window.uploadImage = imgFile => {
      this.afterRead(imgFile);
    };
    // this.loadImg(this.userPhoto);
  },
  methods: {
    // loadImg (url) {
    //   let img = new Image();
    //   img.src = url; // + '&v=' + Math.random()
    //   img.crossOrigin = "*"; // 
    //   img.onload = () => {
    //     let canvas = document.createElement("canvas");
    //     canvas.width = img.width;
    //     canvas.height = img.height;
    //     let ctx = canvas.getContext("2d");
    //     ctx.drawImage(img, 0, 0, img.width, img.height);
    //     let dataURL = canvas.toDataURL("image/png"); //  image/jpeg
    //     if (dataURL === this.errorBase64) this.userPhoto = this.defaultHead;
    //   };
    // },
    // 
    dealImage (imgSrc, quality = 0.3, callback) {
      let newImage = new Image();
      newImage.src = imgSrc;
      newImage.setAttribute("crossOrigin", 'Anonymous'); // url
      let imgWidth, imgHeight;
      newImage.onload = function() {
        imgWidth = this.width;
        imgHeight = this.height;
        let canvas = document.createElement("canvas");
        let ctx = canvas.getContext("2d");
        // if (Math.max(imgWidth, imgHeight) > w) {
        //   if (imgWidth > imgHeight) {
        //     canvas.width = w;
        //     canvas.height = w * imgHeight / imgWidth;
        //   } else {
        //     canvas.height = w;
        //     canvas.width = w * imgWidth / imgHeight;
        //   }
        // } else {
        canvas.width = imgWidth;
        canvas.height = imgHeight;
        // quality = 0.6;
        // }
        ctx.clearRect(0, 0, canvas.width, canvas.height);
        ctx.drawImage(this, 0, 0, canvas.width, canvas.height);

        // let base64 = canvas.toDataURL("image/jpeg", quality); // 
        // console.log(base64.length / 1024, quality);
        // // ,50-150kbquality
        // while (base64.length / 1024 > 150 && quality > 0.1) {
        //   quality -= 0.08;
        //   base64 = canvas.toDataURL("image/jpeg", quality);
        //   console.log(base64.length / 1024, quality);
        // }
        // const blob = base64toBlob(base64);
        // callback(blob);

        // quality
        // while (base64.length / 1024 < 50) {
        // quality += 0.001;
        // base64 = canvas.toDataURL("image/jpeg", quality);
        // }

        // bolb https://developer.mozilla.org/zh-CN/docs/Web/API/HTMLCanvasElement/toBlob
        canvas.toBlob((blob) => {
          // console.log(blob, blob.size, quality);
          if (blob.size > 1024 * 200 && quality > 0.1) {
            this.dealImage(imgSrc, quality - 0.05, callback);
          } else {
            callback(blob);// 
          }
        }, 'image/jpeg', quality); // png
      };
    },
    selectPhoto () {
      sendToAPP(FuncType.albumOpen, "");
    },
    // 
    beforeRead (file) {
      if (
        file.type !== "image/jpeg" &&
        file.type !== "image/png" &&
        file.type !== "image/jpg"
      ) {
        this.$toast(" jpg ");
        return false;
      }
      // 20M
      if (file.size > 1024 * 1024 * 20) {
        this.$toast("20M");
        return false;
      }
      return true;
    },
    afterRead (file) {
      this.option.img = file; // file["content"];
      this.cropperShow = true;
      return false;
    },
    finish (type) {
      // eslint-disable-next-line no-unused-vars
      const that = this;
      // 
//          var test = window.open('about:blank')
//          test.document.body.innerHTML = '..'
      if (type === 'blob') {
        // this.$refs.cropper.getCropBlob((data) => {
        //   // var test = window.open('')
        //   // test.location.href = window.URL.createObjectURL(data)
        // });
        this.$refs.cropper.getCropBlob((data) => {
          this.toUpload(data);
        });
      } else {
        this.$refs.cropper.getCropData((data) => {
          // base64
          this.dealImage(data, 0.5, this.toUpload);
          // console.log(data);
        });
      }
    },
    realTime (data) {
      this.previews = data;
    },
    toUpload (data) {
      const fd = new FormData();
      fd.append("stream", new File([data], 'photo.jpg'));
      fd.append("userID", this.$store.getters.userId);
      this.$mainHttp.user.UploadPhoto(fd).then(res => {
        if (res.Result) {
          this.userPhoto = `${this.$mainHttp.common.downloadURL()}?filename=${this.$store.getters.userId}&fileType=1&platFormType=-10&${new Date().getTime()}`;
          this.option.img = "";
          this.cropperShow = false;
          this.$mainHttp.class.photoUpload({studentID: this.$store.getters.studentID});
          this.$toast("");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("");
        }
      }).catch(() => {
        this.$toast("");
      });
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");

@img-width: 50px;
.myFaceImg {
  margin: 1vw auto 0;

  img {
    width: @img-width;
    height: @img-width;
    border: 1px solid #00cbab;
    border-radius: 50%;
    object-fit: fill;
  }
}

.wrapper {
  position: fixed;
  z-index: 999;
  width: 100%;
  top: 0;
  left: 0;
  height: calc(100% - 50px);

  .vue-cropper {
    background: black;
    background-image: none !important;
  }

  .wrapper_btn {
    height: 50px;
    background: #565656;
    width: calc(100% - 15px * 2);
    padding: 0 15px;

    & a {
      color: white;
      font-size: @font-info;
      display: inline-block;
      line-height: 50px;

      &:nth-of-type(1) {
        float: left;
      }

      &:nth-of-type(2) {
        float: right;
      }
    }
  }
}
</style>



