<template>
  <div id="myZone">
    <head-top title="" :show-back="false"></head-top>
    <div class="myzoneFaceWwall">
      <div class="myFace">
        <div class="myFaceImg">
          <r-photo></r-photo>
        </div>
        <div class="MyFaceName">{{ userName }}</div>
        <div class="maFaceFt"></div>
      </div>
    </div>

    <van-cell-group class="info_list">
      <van-cell class="cell_main" v-for="(item, index) in Details" :key="index" is-link @click="toPath(item.path)">
        <template slot="icon">
          <van-icon cell_icon size="20" :class-prefix="'iconfont ' + item.icon"></van-icon>
        </template>
        <template slot="title">
          <span class="line_title">{{item.info}}</span>
        </template>
      </van-cell>
    </van-cell-group>

    <div class="zone_footer">
      <van-button type="primary" :loading="loading" loading-text="..." @click="toLogout">
        
      </van-button>
    </div>
  </div>
</template>

<script>
import RPhoto from "@/components/r-photo";

export default {
  name: "myZone",
  components: { RPhoto },
  data () {
    return {
      userName: this.$store.getters.student ? this.$store.getters.student["StudentName"] + "" : "",
      loading: false,
      Details: [
        { path: "/personal-data", info: "", icon: "icon-gerenziliao" },
        { path: "/help-use", info: "", icon: "icon-word" },
        { path: "/about-us", info: "", icon: "icon-guanyuwomen" }
      ]
    };
  },
  methods: {
    toLogout () {
      const search = location.href.split('?');
      this.loading = true;
      this.$store.dispatch("Logout").then(() => {
        this.loading = false;
      }).catch(() => {
        this.loading = false;
      });
      if (this.$store.getters.singleLoginReal || this.$store.getters.singleLogOutUrl) {
        window.location.href = `http://logout.html/?${this.$store.getters.singleLogOutUrl}?adr=${search[0]}`;
      }
      this.$router.replace("/login");
    },
    toPath (path) {
      this.$router.push(path);
    }
  }
};
</script>

<style lang="less" scoped>
@import url("./../../styles/common/base.less");

#myZone {
  width: 100%;
  height: 100%;

  .myzoneFaceWwall {
    width: 100%;
    height: 180px;
    margin-bottom: 10px;
    background-image: url("../../assets/pesonbg.png");
    background-repeat: no-repeat;
    background-size: 100vw 156px;
  }

  .myFace {
    width: 73vw;
    height: 120px;
    background: rgba(255, 255, 255, 1);
    position: absolute;
    left: 0;
    right: 0;
    top: 46px;
    margin: 0 auto;

    .MyFaceName {
      margin: 0.4vw auto 0;
      height: 16px;
      font-size: @font-title;
      font-family: MicrosoftYaHei;
      color: rgba(51, 51, 51, 1);
      line-height: 1.2;
    }

    .maFaceFt {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 30px;
      background-image: url("../../assets/facebg.png");
      background-size: 100% 30px;
      background-repeat: no-repeat;
    }
  }

  .info_list {
    background: #fff;

    .icon-gerenziliao {
      color: #21b1f4;
    }

    .icon-word {
      color: #ff976a;
    }

    .icon-tongji {
      color: #fe6b6b;
    }

    .icon-guanyuwomen {
      color: #00cbab;
    }
  }

  .zone_footer {
    position: absolute;
    bottom: 60px;
    left: 18vw;

    .van-button--primary {
      width: 64vw;
      background: rgba(0, 203, 171, 1);
      border-radius: 5px;
      border: 1px solid #00cbab;
    }
  }
}
</style>



